'use client'

import React, { useState } from 'react';
import Link from 'next/link';

const Navbar = () => {
    const [isMenuOpen, setIsMenuOpen] = useState(false);

    const toggleMenu = () => {
        setIsMenuOpen(!isMenuOpen);
    };

    return (
        <nav className="bg-white shadow-md">
            <div className="container mx-auto flex justify-between items-center py-4 px-6">
                {/* Logo */}
                <div className="text-2xl font-bold text-gray-800">
                    <Link href="/">Brand</Link>
                </div>

                {/* Hamburger Menu (Mobile) */}
                <button
                    className="md:hidden text-gray-700 focus:outline-none"
                    onClick={toggleMenu}
                >
                    <i className={`fa ${isMenuOpen ? 'fa-times' : 'fa-bars'}`} aria-hidden="true"></i>
                </button>

                {/* Menu Links */}
                <ul
                    className={`md:flex md:items-center absolute md:static left-0 top-[4.5rem] md:top-auto bg-white md:bg-transparent w-full md:w-auto md:opacity-100 transition-all duration-300 ease-in-out ${
                        isMenuOpen ? 'opacity-100 visible' : 'opacity-0 invisible'
                    }`}
                >
                    <li className="md:mx-4 my-2 md:my-0">
                        <Link href="/">
                            <a className="text-gray-700 hover:text-blue-500 transition">Home</a>
                        </Link>
                    </li>
                    <li className="md:mx-4 my-2 md:my-0">
                        <Link href="/shop">
                            <a className="text-gray-700 hover:text-blue-500 transition">Shop</a>
                        </Link>
                    </li>
                    <li className="md:mx-4 my-2 md:my-0">
                        <Link href="/about">
                            <a className="text-gray-700 hover:text-blue-500 transition">About</a>
                        </Link>
                    </li>
                    <li className="md:mx-4 my-2 md:my-0">
                        <Link href="/blog">
                            <a className="text-gray-700 hover:text-blue-500 transition">Blog</a>
                        </Link>
                    </li>
                    <li className="md:mx-4 my-2 md:my-0">
                        <Link href="/contact">
                            <a className="text-gray-700 hover:text-blue-500 transition">Contact</a>
                        </Link>
                    </li>
                </ul>
            </div>
        </nav>
    );
};

export default Navbar;
